"use strict";

import "babel-polyfill";
import GACE from "./googleAnalyticsCustomEvents";
import SideScroller from "./components/SideScroller";
import { gsap } from "gsap";
import { CSSPlugin, ScrollTrigger } from "gsap/all";
import { TweenMax, TimelineMax } from "gsap/gsap-core";
import * as ScrollMagic from "scrollmagic";

import {
  ScrollMagicPluginIndicator,
  ScrollMagicPluginGsap,
} from "scrollmagic-plugins";
ScrollMagicPluginIndicator(ScrollMagic);
ScrollMagicPluginGsap(ScrollMagic, TweenMax, TimelineMax);

// import "ScrollMagic/scrollmagic/minified/plugins/debug.addIndicators.min.js";
// import "ScrollMagic/scrollmagic/minified/plugins/animation.gsap.min.js";

Array.prototype.shuffle = function () {
  return this.sort(function () {
    return Math.random() - 0.5;
  });
};

Array.prototype.getRandomValue = function () {
  return this.shuffle()[0];
};

Array.prototype.uniqueValues = function () {
  return [...new Set(this)];
};

// Polyfill for nodelist forEach
if (window.NodeList && !NodeList.prototype.forEach) {
  NodeList.prototype.forEach = Array.prototype.forEach;
}
if (window.HTMLCollection && !HTMLCollection.prototype.forEach) {
  HTMLCollection.prototype.forEach = Array.prototype.forEach;
}

//Polyfill for closest
if (window.Element && !Element.prototype.closest) {
  Element.prototype.closest = function (s) {
    var matches = (this.document || this.ownerDocument).querySelectorAll(s),
      i,
      el = this;
    do {
      i = matches.length;
      while (--i >= 0 && matches.item(i) !== el) {}
    } while (i < 0 && (el = el.parentElement));
    return el;
  };
}

document.addEventListener("DOMContentLoaded", function () {
  // set GA-ID
  // const gace = new GACE({
  //   id: "UA-164327129-1",
  // });

  const sidescroller = new SideScroller({
    snapSection: false,
    snapCallback: () => {},
  });

  const controller = new ScrollMagic.Controller({
    vertical: false,
  });

  const tween = TweenMax.to("#target", 5, {
    backgroundColor: "green",
    width: "+=400",
  });

  var scene = new ScrollMagic.Scene({
    triggerElement: "#trigger",
    duration: 500,
  })
    .setTween(tween)
    .addIndicators() // add indicators (requires plugin)
    .addTo(controller);
});
